import React, { useEffect, useState, Fragment  } from "react";
import { makeStyles } from '@material-ui/core/styles';
import CustomTextField from '../../components/CustomTextField'
import { Link } from 'react-router-dom'
import InputLabel from "@material-ui/core/InputLabel";
import Button from '@material-ui/core/Button';
import { Waypoint } from "react-waypoint";
import MUIDataTable from "mui-datatables";
import { Grid, Card, MuiThemeProvider, TablePagination } from "@material-ui/core";
import axios from "axios";
import routes from "../../routes/routes";
import AddIcon from '@material-ui/icons/Add';
import { Autocomplete } from "@material-ui/lab";
import { useSelector } from "react-redux";
import logo from '../../Assets/Img/logo.png'
import { createNotification } from "../../components/Toast";
import {
  Chip,
  TextField,
} from "@material-ui/core";



const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: '97%',
    paddingLeft: '4%',
    paddingTop: '1%',
    backgroundColor: "#F9FAFC",
  },
  billingDD: {
    width: '97%',
    padding: '0px'

  },
}));




let productData = [];


const CreateOrder = (props) => {
  const classes = useStyles();
  const [allProducts, setAllProducts] = React.useState([]);
  const [page, setPage] = useState(0);
  const [page2, setPage2] = useState(0);
  const [totalCount, setTotalCount] = useState(0);

  const [rowsPerPage, setRowsPerPage] = useState(100);
  const [rowsPerPage2, setRowsPerPage2] = useState(100);
  const [orderNO, setOrderNumber] = useState();
  const [error, setError] = useState(false);
  const [date, setDate] = useState();
  const [expectedDate, setExpectedDate] = useState();
  const [billTo, setBillTo] = useState();
  const [shipTo, setShipTo] = useState();
  const [orderFee, setOrderFee] = useState();
  const [orderFreight, setOrderFreight] = useState();
  const [orderDiscount, setOrderDiscount] = useState();
  const [productId, setProductId] = useState();
  const [perantCode, setPerantCode] = useState();
  const [product, setProduct] = useState([]);
  const [data, setData] = useState([]);
  const [quantity, setQuantity] = useState([]);
  const [tax, setTax] = useState([]);
  const [cost, setCost] = useState([]);
  const [purchaseOrder, setPurchaseOrder] = useState();
  const [vendorData, setVendorData] = useState([]);
  // const [data, setData] = useState([]);



  const [vendor, setVendor] = useState([]);
  const [location, setLocation] = useState([]);
  const [company, setCompany] = useState([]);

  const [assignVendor, setAssignVendor] = useState([]);
  const [allVendors, setAllVendors] = useState([]);
  const [allVariants, setAllVariants] = useState([]);
  const [vendorList, setVendorList] = useState([]);
  const [preview, setPreview] = useState(false);

  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);


  const getAllLocations = async () => {
    const pData = axios({
      method: "get",
      url: routes.api.location.getLocationsList.url,
      params: { limit: rowsPerPage, page: page },
      headers: { Authorization: "Bearer " + userToken },
    });
    const [location] = await axios.all([pData]);
    if (location) {
      setLocation(location.data)
    }
  }
  console.log('location', location)
  const getCompanyInformation = async ()=>{
    
    const pData = axios({
      method: "get",
      url: routes.api.companyInfo.getCompanyInfo.url,
      params: { limit: rowsPerPage, page: page },
      headers: { Authorization: "Bearer " + userToken },
    });

    const [companyData] = await axios.all([pData]);
    if (location) {
      setCompany(companyData.data)
    }


  }
  console.log("company", company)
  const getAllProducts = async () => {
    // setIsActive(true)
    // setRefresh(true);

    const pData = axios({
      method: "get",
      url: routes.api.product.getAllProducts.url,
      params: { limit: rowsPerPage, page: page },
      headers: { Authorization: "Bearer " + userToken },
    });

    const [product] = await axios.all([pData]);
    if (product.data.success) {
      // console.log("product", product.data)
      setTotalCount(product.data.totalDoc);
      product.data.doc.map((data, index) => {
        const newdata = {
          _id: data._id,
          productName: data.name,
          // name:` ${data.name} Sku:   ${data.sku} ,Quantity: ${data.quantity}`,
          parantCode: data.parantCode
        };
        productData = [...productData, newdata];
        return productData;
      });
      // console.log("productData", productData)
      setAllProducts(productData);
    }
    // setIsActive(false)
  };
  const handleChangeTableInputs = (event, rowIndex, tableMeta) => {

    // setQuantity(event.target.value)
    const newProduct = [...product]
    const item = newProduct[rowIndex];
    const newObject = { ...item };
    newObject["quantity"] = event.target.value;
    newProduct[rowIndex] = newObject;
    // setProduct(newObject)
    // let array = product;
    // array.push(newObject);
    setProduct(newProduct)
  }
  const handleChangeCost = (event, rowIndex, tableMeta) => {
    // setCost(event.target.value)
    const newProduct = [...product]
    const item = newProduct[rowIndex];
    const newObject = { ...item };
    newObject["cost"] = event.target.value;
    newProduct[rowIndex] = newObject;

    setProduct(newProduct)


  }
  const handleChangeTax = (event, rowIndex, tableMeta) => {
    // setTax(event.target.value)
    const newProduct = [...product]
    const item = newProduct[rowIndex];
    const newObject = { ...item };
    newObject["tax"] = event.target.value;
    newProduct[rowIndex] = newObject;
    setProduct(newProduct)


  }


const handleClose =() =>{
  setPreview(false)
}
const handleSubmit =()=>{
  axios
  .post(routes.api.POorders.createPoOrder.url, {
    purchaseOrder,
    vendorData
  },
  {headers: {Authorization: "Bearer " + userToken}}
  )
  .then(function (response) {
    console.log("response", response)
    if(response.data.success){
      createNotification("success", "Created Successfully");
    }
    // setLoading(false);
    
  })
  .catch(function (error) {
    console.log(error);
  });
}
  const handlePreview = () => {
    setPreview(true)
    // console.log("product", product)
    const newObject ={
      orderNumber:orderNO,
      date:date,
      expectedDate :expectedDate,
      fee:orderFee,
      discount:orderDiscount,
      billTo:billTo,
      shipTo:shipTo,
    }
    let proderByVendor = []

    const VendorListids = vendorList.map((o) => o._id);
    const filteredVendorList = vendorList.filter(
      ({ _id }, index) => !VendorListids.includes(_id, index + 1)
    );
    let vendorData = []
    filteredVendorList.forEach((vendor, inf) => {
      let vendorObj = { ...vendor, 'products': [] }
      product.forEach((pdt, i) => {
        if (vendor._id === pdt.vendor_id) {

          vendorObj.products.push(pdt)
        }
        if (i === product.length - 1) {
          vendorData.push(vendorObj)
        }
      })
    })
    setPurchaseOrder(newObject)
    setVendorData(vendorData)
    

  }
  console.log("purchaseOrder", purchaseOrder)
  console.log("vendorData", vendorData)

  const handleChangeAutoComplete = (event, value, rowIndex, tableMeta) => {
    event.preventDefault()
    let newarr = [...product];
    let rowData = newarr[rowIndex]
    // console.log("values", value)
    let vendorId = value._id;
    rowData['vendor_id'] = vendorId;
    newarr[rowIndex] = rowData;
    let vList = [...vendorList];
    if (!!vList.length) {
      vList = [...vList, value]

      const VendorListids = vList.map((o) => o._id);
      const filteredVendorList = vList.filter(
        ({ _id }, index) => !VendorListids.includes(_id, index + 1)
      );
      setVendorList(filteredVendorList)
    } else {
      setVendorList([...vendorList, value])

    }
    setProduct(newarr)

  }

  const handleAutoCompleteSelectProduct = (event, data) => {
    event.preventDefault()
    setData(data)
    setProductId(data._id)
    setPerantCode(data.parantCode)
  }
  const handleAutoCompleteBillingLocation = (event, data) => {
    event.preventDefault()
    setBillTo(data._id)

  }
  const handleAutoCompleteShippingLocation = (event, data) => {
    event.preventDefault()
    setShipTo(data._id)

  }

  let SelectedProduct = []

  const HandleAddProduct = async (e) => {
    e.preventDefault();
    let VendorsData = [];
    const pData = axios({
      method: "get",
      url: routes.api.product.getProductById.url,
      params: { _id: productId },
      headers: { Authorization: "Bearer " + userToken },
    });
    const [variants] = await axios.all([pData]);

    if (variants.data.success) {
      variants.data.doc.map((data, index) => {
        const newdata = {
          _id: data._id,
          barcode: data.barcode,
          name: data.name + data.sku + data.barcode,
          productName: data.name,
          parl: data.parl,
          price: data.price,
          sku: data.sku,
          barcode: data.barcode,
          status: data.status,
          imageName: data.imageName,
        };
        VendorsData = [...VendorsData, ...data.vendor_id];

        variantData = [...variantData, newdata];
        return variantData;
      });
      const ids = VendorsData.map((o) => o._id);
      const filtered = VendorsData.filter(
        ({ _id }, index) => !ids.includes(_id, index + 1)
      );
      setAllVendors(filtered);
      setProduct(variantData)

    }
  }
  const getAllVendors = async () => {
    let vendorData = [];
    // setIsActive(true);
    // setRefresh(true);
    const pData = axios({
      method: "get",
      url: routes.api.vendor.getAllVendors.url,
      params: { limit: rowsPerPage, page: page },
      headers: { Authorization: "Bearer " + userToken },
    });
    const [product] = await axios.all([pData]);
    setTotalCount(product.data.totalDoc);
    product.data.doc.map((data, index) => {
      const newdata = {
        _id: data._id,
        dataIndex: index + 1,
        name: data.name,
        description: data.description,
        venorImage: data.vengorImage,
        contactName: data.contactName,
        contactEmail: data.contactEmail,
        orderEmail: data.orderEmail,
        orderFax: data.orderFax,
        phone: data.phone,
      };

      vendorData = [...vendorData, newdata];
      return vendorData;
    });
    setVendor(vendorData);
    // setIsActive(false);
  };

  let variantData = []
  const HandleAddProductVariants = async (event) => {
    event.preventDefault();
    let VendorsData = [];
    const pData = axios({
      method: "get",
      url: routes.api.product.getVariantsByProductID.url,
      params: { parantCode: perantCode },
      headers: { Authorization: "Bearer " + userToken },
    });
    const [variants] = await axios.all([pData]);

    if (variants.data.success) {
      variants.data.doc.map((data, index) => {
        const newdata = {
          _id: data._id,
          productName: data.name,
        };
        VendorsData = [...VendorsData, ...data.vendor_id];

        variantData = [...variantData, newdata];
        return variantData;
      });
      const ids = VendorsData.map((o) => o._id);
      const filtered = VendorsData.filter(
        ({ _id }, index) => !ids.includes(_id, index + 1)
      );
      setAllVendors(filtered);
      setProduct(variantData)

    }
  };

  useEffect(() => {
    getAllLocations()
    getAllProducts();
    getAllVendors()
    getCompanyInformation()

  }, [data]);
  const columns = [

    // All Variants
    {
      name: "productName",
      label: "Product Name",
      options: {
        filter: true,
        // sort: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          // console.log(tableMeta.rowData, "rowtableMeta")
          return (
            <>
              {tableMeta.rowIndex === allProducts.length - 5 ? (
                <Waypoint
                  onEnter={() => {
                    setPage(page + 1);
                    // setRowsPerPage(rowsPerPage+500)
                  }}
                />
              ) : null}
              <div>
                {tableMeta.rowData[0]}
                {/* <br />
                  {tableMeta.rowData[2]}
                  <br />
                  SKU {tableMeta.rowData[3]}
                  <br />
                  Qty in stock: {tableMeta.rowData[4]} */}
              </div>
            </>
          );
        },
      },
    },
    // action

    {
      name: "assignVendor",
      label: "Vendors",
      options: {
        setCellProps: () => ({ style: { minWidth: "300px", maxWidth: "300px" } }),
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div className="pt-3 inputBlock">
              
              <Autocomplete
                id="combo-box-demo"
                options={vendor}
                // onClick={}
                getOptionLabel={(option) => option.name}
                style={{ width: 250, padding: '0px' }}
                // getOptionDisabled={(option) => option.name === value.name? true :false}
                renderInput={(params) => <TextField
                
                  {...params}
                  variant="outlined" />}
                onChange={(event, value) => handleChangeAutoComplete(event, value, tableMeta.rowIndex, tableMeta)}
              />
            </div>
          );
        },
      },
    },
    {
      name: "quantity",
      label: "Quantity",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div className="pt-3 inputBlock">
              <input
                type="text"
                defaultValue={value}
                onChange={(events) => handleChangeTableInputs(events, tableMeta.rowIndex, tableMeta)}
                className="form-control"
              />
            </div>
          );
        },
      },
    },
    {
      name: "cost",
      label: "Cost",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div className="pt-3 inputBlock">
              <input
                type="text"
                defaultValue={value}
                onChange={(events) => handleChangeCost(events, tableMeta.rowIndex, tableMeta)}
                className="form-control"
              />
            </div>
          );
        },
      },
    },

    {
      name: "tax",
      label: "Tax",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div className="pt-3 inputBlock">
              <input
                type="text"
                defaultValue={value}
                onChange={(events) => handleChangeTax(events, tableMeta.rowIndex, tableMeta)}
                className="form-control"
              />

            </div>
          );
        },
      },
    },
  ];
  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
  };
  return (
    <div className={classes.root}>
      {!preview ? (  
       <>
        <header className="headerCreateOrderPurchase">
          <div className="header__row">
            <div className="Texts_Display">
              Create Purchase Order
                </div>
          </div>
        </header>
        <div className="row">
          <div className="col-lg-8">
            <section className="leftColumn">
              <section className="eMmHox">
                <div className="fxrGze">
                  <div className="card-title">
                    <div className="hPSGav">
                      Order Detail
                </div>
                  </div>
                  <div className="dEgCwM">
                    {/* <Button>Add Item</Button> */}
                  </div>
                </div>
                <div className="bLLHWD">
                  <div className="">
                    {/* <img src ={itemImage}/> */}
                    <div className="pt-3 inputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Order number
              </InputLabel>
                      <CustomTextField
                        // value={dob}
                        autoComplete={false}
                        type="text"
                        placeholder="Ex. MO-01"
                        dummyClass="w-85"
                        // error={error}
                        // message={dobError}
                        onChange={(e) => {
                          setOrderNumber(e.target.value);
                          setError(false);
                        }}
                      />
                    </div>
                    <div className="pt-3 inputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Date
              </InputLabel>
                      <CustomTextField
                        // value={dob}
                        autoComplete={false}
                        type="date"
                        placeholder="Ex. MO-01"
                        dummyClass="w-85"
                        // error={error}
                        // message={dobError}
                        onChange={(e) => {
                          setDate(e.target.value);
                          setError(false);
                        }}
                      />
                    </div>
                    <div className="pt-3 inputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        EXPECTED BY
            </InputLabel>
                      <CustomTextField
                        // value={dob}
                        autoComplete={false}
                        type="Date"
                        placeholder="Ex. MO-01"
                        dummyClass="w-85"
                        // error={error}
                        // message={dobError}
                        onChange={(e) => {
                          setExpectedDate(e.target.value);
                          setError(false);
                        }}
                      />
                    </div>

                  </div>
                </div>
              </section>
            </section>
          </div>
          <div className="col-lg-4">
            <section className="rightColumn">
              <section className="eMmHox">
                <div className="fxrGze">
                  <div className="card-title">
                    <div className="hPSGav">
                      Shipping Method

                </div>
                  </div>
                  <div className="dEgCwM">
                    {/* <Button>Add Item</Button> */}
                  </div>
                </div>
                <div className="order-detail-wrapper">
                  <div className="flex-column w-full">


                    <div className=" inputBlock pt-3">
                      <div class="form-group w-85">
                        <label for="exampleFormControlSelect1">Bill To</label>
                        <Autocomplete
                          id="combo-box-demo"
                          options={location}

                          // onClick={}
                          getOptionLabel={(option) => option.country}
                          style={{ width: 300, padding: '0px' }}
                          renderInput={(params) => <TextField
                            className="billingDD"
                            InputProps={{
                              className: classes.billingDD
                            }}
                            {...params}
                            // onChange={(event)=>handleChangeAutoComplete(event, allProducts)}


                            variant="outlined" />}
                          onChange={(event, value) => handleAutoCompleteBillingLocation(event, value)}
                        />
                      </div>
                    </div>
                    <div className=" inputBlock pt-3 pb-2">
                      <div class="form-group w-85">
                        <label for="exampleFormControlSelect1">Ship To</label>
                        <Autocomplete
                          id="combo-box-demo"
                          options={location}
                          // onClick={}
                          getOptionLabel={(option) => option.country}
                          style={{ width: 300, padding: '0px' }}
                          renderInput={(params) => <TextField

                            {...params}
                            // onChange={(event)=>handleAutoCompleteShippingLocation(event, location)}


                            variant="outlined" />}
                          onChange={(event, value) => handleAutoCompleteShippingLocation(event, value)}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </section>
            </section>
          </div>
        </div>
        <div className="row mt-3">
          <div className="col-lg-8">
            <div style={{ display: 'flex', flexDirection: "row", justifyContent: 'flex-end', width: '100%' }}>
              <div className="  pt-3 pb-2" style={{ width: '40%' }}>
                <div class="form-group" style={{ display: 'flex', flexDirection: 'rows' }}>

                  {/* <select className="form-control">
                  <option value="volvo">Volvo</option>
                  <option value="saab">Saab</option>
                  <option value="opel">Opel</option>
                  <option value="audi">Audi</option>
                </select> */}
                  <Autocomplete
                    id="combo-box-demo"
                    options={allProducts}
                    // onClick={}
                    getOptionLabel={(option) => option.productName}
                    style={{ width: 300, padding: '0px' }}
                    renderInput={(params) => <TextField

                      {...params}
                      // onChange={(event)=>handleChangeAutoComplete(event, allProducts)}


                      variant="outlined" />}
                    onChange={(event, value) => handleAutoCompleteSelectProduct(event, value)}
                  />
                </div>
              </div>
              <Button
                // onClick={deleteProduct}
                className="buttonColorAdd12"
                variant="contained"
                // onClick={(event)=>handleSubmitVendor(event)}
                style={{ fontSize: '16px' }}
              >
                <AddIcon onClick={(event) => HandleAddProduct(event)} />
              </Button>
              <Button
                // onClick={deleteProduct}
                className="buttonColorAdd12"
                variant="contained"
                onClick={(event) => HandleAddProductVariants(event)}
              >
                Add All Variants
            </Button>
            </div>

            <MuiThemeProvider>
              <MUIDataTable
                style={{ th: { backgroundColor: "grey" } }}

                //   title="Employees List"
                data={product}
                columns={columns}
                options={option}
              />
              <TablePagination
                component="div"
              // count={totalCount}
              // page={page}
              // onPageChange={handleChangePage}
              // rowsPerPage={rowsPerPage}
              // onRowsPerPageChange={handleChangeRowsPerPage}
              />
            </MuiThemeProvider>
          </div>
          <div className="col-lg-4">
            <section className="rightColumn">
              <section className="eMmHox">
                <div className="fxrGze">
                  <div className="card-title">
                    <div className="hPSGav">
                      Billing Information

                </div>
                  </div>
                  <div className="dEgCwM">
                    {/* <Button>Add Item</Button> */}


                  </div>


                </div>
                <div className="order-detail-wrapper">
                  <div className="flex-column w-full">
                    <div className="pt-3 inputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Fee
                    </InputLabel>
                      <CustomTextField
                        // value={dob}
                        autoComplete={false}
                        type="text"
                        // placeholder="Ex. MO-01"
                        dummyClass="w-85"
                        // error={error}
                        // message={dobError}
                        onChange={(e) => {
                          setOrderFee(e.target.value);
                          setError(false);
                        }}
                      />
                    </div>
                    <div className="pt-3 inputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Freight
          </InputLabel>
                      <CustomTextField
                        // value={dob}
                        autoComplete={false}
                        type="text"
                        // placeholder="Ex. MO-01"
                        dummyClass="w-85"
                        // error={error}
                        // message={dobError}
                        onChange={(e) => {
                          setOrderFreight(e.target.value);
                          setError(false);
                        }}
                      />
                    </div>
                    <div className="pt-3 inputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        PO Discount
          </InputLabel>
                      <CustomTextField
                        // value={dob}
                        autoComplete={false}
                        type="text"
                        // placeholder="Ex. MO-01"
                        dummyClass="w-85"
                        // error={error}
                        // message={dobError}
                        onChange={(e) => {
                          setOrderDiscount(e.target.value);
                          setError(false);
                        }}
                      />
                    </div>
                  </div>
                </div>
              </section>
            </section>
          </div>
        </div>
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', paddingBottom: '5%' }}>
          <Button
            // onClick={deleteProduct}
            className="buttonColorAdd12"
            variant="contained"
            onClick={() => handlePreview()}
            style={{ fontSize: '16px' }}
          >
            Preview
            </Button>
        </div>
      </>):(
      <>
        <div style={{display:"flex", flexDirection:"row", justifyContent:"space-between"}}>
        <div style={{display:"flex", flexDirection:"column", justifyContent:"center", alignItems:"center"}}>
          <img src={logo} style={{width:"80%", height:'50%'}}/>
          <span className="text-bold pb-2">
            {company&& company.companyName}
            </span>
            <span>
            {company&& company.physicalAdressState}
            </span>
        </div>
        <div style={{display:"flex", flexDirection:"column", justifyContent:"center", alignItems:"center", width:'50%'}}>
        <div className="pt-3 PreviewBlockTwo">

        <span className="text-black text-14 pt-2 SpanBlockTwo text-bold ">
          Status
        </span>
        <span className="text-black text-14 pt-2 SpanBlockTwo ">
          Over Due
        </span>

        </div>
        <div className="pt-3 PreviewBlockTwo">
        <span className="text-black text-14 pt-2 SpanBlockTwo text-bold">PO Number</span>
        <span className="text-black text-14 pt-2  SpanBlockTwo ">
          {orderNO && orderNO}
        </span>

      </div>
      <div className="pt-3 PreviewBlockTwo" >
      <span className="text-black text-14 pt-2 SpanBlockTwo text-bold ">
        Date
      </span>
      <span className="text-black text-14 pt-2 SpanBlockTwo ">
      {data && date}
      </span>

      </div>

      <div className="pt-3 PreviewBlockTwo" >
      <span className="text-black text-14 pt-2 SpanBlockTwo text-bold">
       Expected Date
      </span>
      <span className="text-black text-14 pt-2 SpanBlockTwo ">
      {expectedDate && expectedDate}
      </span>
      </div>
      </div>
    </div>
        <div>
        <div className="row">
          <div className="col-lg-12">
            <section className="leftColumn">
              <section className="eMmHox">
                <div className="fxrGze">
                  <div style={{width:'100%'}}>
                    <div className="hPSGav">
                    <div style={{width:'100%', display:'flex', flexDirection:'row'}}>
                      <div style={{display:'flex', flexDirection:'row', justifyContent:'space-around', width:'40%'}}>
                        <span className="text-black text-14 pt-2 SpanBlockTwo text-bold ml-3">
                          Ship To
                        </span>
                        <input
                        className='mt-1 ml-3'
                        value={location && location[0] && location[0].country}
                        disabled
                        // value={location ? location[0].country:''}
                          style={{width:'100%', borderRadius:'5px', fontSize:'14px'}}/>

                      </div>
                          <div style={{display:'flex', flexDirection:'row', justifyContent:'space-around', width:'40%'}}>
                        <span className="text-black text-14 pt-2 SpanBlockTwo text-bold ml-3">
                          Bill To
                        </span>
                        <input
                        className='mt-1 ml-3'
                        value={location && location[0] && location[0].country}
                        disabled
                        style={{width:'100%', borderRadius:'5px', fontSize:'14px'}}/>

                      </div>
                      </div>

                </div>
                  </div>
           
                </div>
             
              </section>
            </section>
          </div>
   

        </div>
        <div className="row">
          <div className="col-lg-12">
            <section className="leftColumn">
              <section className="eMmHox">
                <div className="fxrGze">
                  <div className="card-title">
                    <div className="hPSGav">
                      Product Detail
                </div>
                  </div>
           
                </div>
                 <div className="row">
                   <div className="col-lg-12">
                   <table id="customers" className="cardBoxOne" style={{width:'100%'}}>
                  
                  <tr>
                    <th className="vendorHeading">Product Name</th>
                    <th className="vendorHeading">Quantity</th>
                    <th className="vendorHeading">Discount</th>
                    <th className="vendorHeading">Tax</th>
                  </tr>
                  
                  {vendorData && vendorData.map((item, i) => {
                  return (
                    <Fragment>
                      
                      <tr key={i} >
                        <th  colSpan='4'>{`${item.name} ,  ${item.contactEmail} ,  ${item.phone}`}</th>
       
                      </tr>
                      {item && item.products && item.products.map((product, ind) => {
                        return (
                          <Fragment>
                            <tbody key={ind}>
                              <tr >
                                <td>{product.productName}</td>
                                <td>{product.quantity}</td>
                                <td>{product.cost}</td>
                                <td>{product.tax}</td>
                              </tr>
                              </tbody>
                          </Fragment>
                        );
                       })}
                    </Fragment>
                  );
                })}
                  {/* <tr>
                    <th className="vendorHeading">
                    Vendor
                  </th>
                  <th className="vendorHeading">
                    
                  </th>
                  <th className="vendorHeading">
                    
                  </th>
                  <th className="vendorHeading">
                    
                  </th>
                    </tr>
                  <tr>
                    <td>Alfreds Futterkiste</td>
                    <td>Maria Anders</td>
                    <td>Germany</td>
                    <td>Germany</td>
                  </tr> */}

                </table>
                   </div>
                  </div>
              </section>
            </section>
          </div>
        </div>
      </div>
        {/* <div id="scissors"></div> */}
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', paddingBottom: '5%' }}>
          <Button
            // onClick={deleteProduct}
            className="buttonColorAdd12 ml-2"
            variant="contained"
            onClick={() => handleClose()}
            style={{ fontSize: '16px' }}
          >
            Close
            </Button>
            <Button
            // onClick={deleteProduct}
            className="buttonColorAdd12"
            variant="contained"
            onClick={() => handleSubmit()}
            style={{ fontSize: '16px' }}
          >
            Save
            </Button>

        </div>

      </>)}

    </div>
  );
}
export default CreateOrder;